//Component
import Logo from "../components/Logo/Logo";
import AuthInput from "../components/AuthInput/AuthInput";
//react-bootstrap
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
//React
import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";

import Swal from "sweetalert2";

import { useAuth } from "../contexts/AuthContext";

import { validAccount, validPassword, validEmail } from "../helpers/regex";

const RegisterPage = () => {
  const navigate = useNavigate();
  const { register, isAuthenticated, currentMember } = useAuth();

  const [registerPage, setRegisterPage] = useState({
    account: "",
    name: "",
    email: "",
    password: "",
    checkPassword: "",
  });

  const handleInput = (keyName) => (currentValue) => {
    setRegisterPage({
      ...registerPage,
      [keyName]: currentValue,
    });
  };

  // const handleErrorMessage = () => {
  //   if (!register.account) return "";
  // };

  const handleClick = async (e) => {
    e.preventDefault();

    //
    if (
      !(
        registerPage.account &&
        registerPage.name &&
        registerPage.email &&
        registerPage.password &&
        registerPage.checkPassword
      )
    ) {
      Swal.fire({
        position: "top",
        title: "",
        text: "",
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      return;
    }

    if (
      !(
        registerPage.account.match(validAccount) &&
        registerPage.password.match(validPassword)
      )
    ) {
      Swal.fire({
        position: "top",
        title: "",
        text: "",
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      return;
    }

    if (!registerPage.email.match(validEmail)) {
      Swal.fire({
        position: "top",
        title: "",
        text: "Email",
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      return;
    }

    const success = await register({
      account: registerPage["account"],
      name: registerPage["name"],
      email: registerPage["email"],
      password: registerPage["password"],
      checkPassword: registerPage["checkPassword"],
    });

    if (success) {
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      return;
    }
    // Swal.fire({
    //   position: "top",
    //   title: "",
    //   timer: 1000,
    //   icon: "error",
    //   showConfirmButton: false,
    // });
  };

  useEffect(() => {
    console.log("[Signup useEffect] isAuthenticated:", isAuthenticated);
    if (isAuthenticated) {
      navigate(`/${currentMember.id}`);
    }
  }, [navigate, isAuthenticated, currentMember]);

  return (
    <Container>
      <Row className="justify-content-center">
        <Col md={6} lg={4}>
          <Logo title="" />
          <form action="">
            <AuthInput
              value={register.account}
              onChange={handleInput("account")}
              placeholder=""
              error={
                registerPage.account.match(validAccount)
                  ? ""
                  : ""
              }
            />
            <AuthInput
              label=""
              value={register.name}
              onChange={handleInput("name")}
              placeholder=""
            />
            <AuthInput
              label="Email"
              type="email"
              value={register.email}
              onChange={handleInput("email")}
              placeholder="Email"
              error={
                registerPage.email.match(validEmail) ? "" : "email"
              }
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="new-password"
              value={register.password}
              onChange={handleInput("password")}
              placeholder=""
              error={
                registerPage.password.match(validPassword)
                  ? ""
                  : ""
              }
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="new-password"
              value={register.checkPassword}
              onChange={handleInput("checkPassword")}
              placeholder=""
              error={
                registerPage.checkPassword.match(validPassword)
                  ? ""
                  : ""
              }
            />
            <div className="mb-3">
              <button
                onClick={handleClick}
                className="btn btn-primary w-100 text-white rounded-pill"
              >
                
              </button>
            </div>
          </form>

          <div className="d-flex justify-content-center">
            <Link to="/login"></Link>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default RegisterPage;
