//Component
import AuthInput from "../components/AuthInput/AuthInput";
import Logo from "../components/Logo/Logo";
//react-bootstrap
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
//React
import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
//context
import { useAuth } from "../contexts/AuthContext";
//plugin
import Swal from "sweetalert2";

const AdminPage = () => {
  const navigate = useNavigate();
  const { adminLogin, isAuthenticated } = useAuth();
  const [adminLoginPage, setAdminLoginPage] = useState({
    account: "",
    password: "",
  });

  const handleInput = (keyName) => (currentValue) => {
    setAdminLoginPage({
      ...adminLoginPage,
      [keyName]: currentValue,
    });
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    // console
    //
    if (!(adminLoginPage.account && adminLoginPage.password)) return;
    //Login api
    const success = await adminLogin({
      account: adminLoginPage["account"],
      password: adminLoginPage["password"],
    });
    //sweetAlert
    if (success) {
      // 
      // navigate("")
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      return;
    }

    Swal.fire({
      position: "top",
      title: "",
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
  };

  useEffect(() => {
    console.log("[adminLoginPage useEffect] isAuthenticated:", isAuthenticated);
    if (isAuthenticated) {
      navigate("/admin_main");
    }
  }, [navigate, isAuthenticated]);

  return (
    <Container>
      <Row className="justify-content-center">
        <Col md={6} lg={4}>
          <Logo title={""} />
          <form action="">
            <AuthInput
              value={adminLoginPage.account}
              onChange={handleInput("account")}
              placeholder=""
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="current-password"
              value={adminLoginPage.password}
              onChange={handleInput("password")}
              placeholder=""
            />
            <div className="mb-3">
              <button
                onClick={handleLogin}
                className="btn btn-primary w-100 text-white rounded-pill"
              >
                
              </button>
            </div>
          </form>
          <div className="d-flex justify-content-end">
            <Link to="/login"></Link>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default AdminPage;
