import axios from "axios";

import Swal from "sweetalert2";
const BASE = "https://twitter-api-trww6pqemq-de.a.run.app";
// const BASE = "https://shielded-brook-33484.herokuapp.com";

const AUTH_URL = BASE + "/api/";

export const login = async ({ account, password }) => {
  console.log({ account, password });
  try {
    const { data } = await axios.post(AUTH_URL + "signin", {
      account,
      password,
    });
    const { token } = data.data;
    console.log("[]", data);
    if (token) return { success: true, ...data };
    return data;
  } catch (error) {
    const errorMessage = () => {
      if (error.response.data.message === "This account has not registered.") {
        return "";
      }
      if (
        error.response.data.message === "Account or password is not correct."
      ) {
        return "";
      }

      if (error.response.data.message === "Permission denied.") {
        return "";
      }
    };

    Swal.fire({
      position: "top",
      title: "",
      text: errorMessage(),
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
    console.error(error);
    console.log("[]", error.response.data.message);
    return { status: false };
  }
};

export const register = async ({
  account,
  name,
  email,
  password,
  checkPassword,
}) => {
  try {
    const { data } = await axios.post(AUTH_URL + "users", {
      account,
      name,
      email,
      password,
      checkPassword,
    });

    const { token } = data.data;
    if (token) return { success: true, ...data };
    return data;
  } catch (error) {
    const errorText = () => {
      if (error.response.data.message === "All field are required!") {
        return "";
      }
      if (
        error.response.data.message ===
        "Password and confirmPassword do not match."
      ) {
        return "";
      }
      if (error.response.data.message === "Email input is invalid!") {
        return "email";
      }
      if (
        error.response.data.message ===
        "Name field has max length of 50 characters."
      ) {
        return "";
      }
      if (error.response.data.message === "Account already exists!") {
        return "";
      }
      if (error.response.data.message === "Email already exists!") {
        return "email";
      }
    };

    console.error(error);
    console.log("[]: ", errorText);
    Swal.fire({
      position: "top",
      title: "",
      text: errorText(),
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
    return { status: false };
  }
};

export const checkPermission = async (authToken) => {
  try {
    const response = await axios.get(AUTH_URL + "auth/test-token", {
      headers: {
        Authorization: "Bearer " + authToken,
      },
    });

    return response.status;
  } catch (error) {
    console.error(error);
    console.log("[Check Permission Failed]:", error);
  }
};

