import { useState } from "react";
import styles from "./AuthInput.module.scss";

/*
props
type (string)
label(string)
placeholder (string)
defaultValue (string)
onChange(function)
maxChar(Number or String) => 
autocomplete => 
error(string) => 0
*/

/*
  handler input value
  //hook
  const [login, setLogin] = useState({
    account: "",
    password: "",
  });

  const handleInput = (keyName) => (currentValue) => {
    setLogin({
      ...login,
      [keyName]: currentValue,
    });
  };

  const handleInput = (keyName) => (currentValue) => {
    setAdminLogin({
      ...adminLogin,
      [keyName]: currentValue,
    });
  };
   return(
    <>
    <AuthInput value={adminLogin.adminId} onChange={handleInput("adminId")} />
    <AuthInput label="" type="password" value={adminLogin.adminPassword} onChange={handleInput("adminPassword")}/>
    </>
    )
*/

const AuthInput = ({
  type,
  label,
  placeholder,
  defaultValue,
  onChange,
  maxChar,
  autoComplete,
  error,
  defaultCount = 0,
}) => {
  const [textCount, setTextCount] = useState(defaultCount);
  return (
    <div className={`${styles["area"]} ${error ? styles["error"] : ""}`}>
      <div className={`${styles["AuthInput"]}`}>
        <label htmlFor={label || "account"}>{label ? label : ""}</label>
        <input
          id={label || "account"}
          defaultValue={defaultValue || ""}
          type={type ? type : "text"}
          placeholder={placeholder}
          maxLength={maxChar ? maxChar : 50}
          // pattern={type === "email" && ".+@globex.com"}
          onChange={(e) => {
            setTextCount(e.target.value.length);
            return onChange?.(e.target.value);
          }}
          autoComplete={autoComplete}
          required
        />
        <div className={`${styles["footer"]}`}>
          <p className="mb-0">{error ? error : " "}</p>
          <small className="mb-0 text-light">
            {textCount}/ {maxChar ? maxChar : 50}
          </small>
        </div>
      </div>
    </div>
  );
};

export default AuthInput;
